@use "global" as *;
.content__content {
  // size
  display: block;
  max-width: rem(187.5);
  width: 100%;
  // decoration -> `.content__content::before`
  background-color: #f00;
  overflow: hidden;
  // ajustment
  position: relative;
  @include mq(md) {
    // size
    max-width: rem(320);
    width: 100%;
  }
  @include mq(xl) {
  }
}

.content__content::before {
  content: "";
  // size
  display: block;
  width: 100%;
  padding-top: 89.3617%;
  @include mq(md) {
  }
  @include mq(xl) {
  }
}

.content__content::after {
  content: "";
  // size
  display: block;
  width: 100%;
  height: 100%;
  // ajustment - `.content__content`
  position: absolute;
  top: 0;
  left: 0;
  // decoration
  background-color: rgba(255, 255, 255, 0);
  @include mq(md) {
  }
  @include mq(xl) {
  }
}

.content__content:hover::after {
  // decoration
  background-color: rgba(255, 255, 255, 0.6);
  @include mq(md) {
  }
  @include mq(xl) {
  }
}
